import {DivWhiteRoundedShadowed} from '../common/DivWhiteRoundedShadowed';

import {Col, Row} from 'react-bootstrap';
import {K} from '../../Constants';
import React, {useState} from 'react';
import {aidaValidation, richiestaPostFromForm} from '../../http/api';
import {ErrorMessage, Field, Form, Formik} from 'formik';
import * as Yup from 'yup';
import ReCAPTCHA from 'react-google-recaptcha';
import  {NumericFormat} from 'react-number-format';

export const Richiesta = () => {

  const [aida, setAida] = useState();
  const [aidaRes, setAidaRes] = useState();


  // const getValidation = (cf => {
  //   console.log('validation')
  //   return aidaValidation(K.serviceUrl, cf)
  //     .then(value => {
  //       console.log(value);
  //       setAida(value);
  //     })
  //
  // })


  const phoneRegExp = /^(([+]|00)39)?((3[1-6][0-9]))(\d{7})$/;
  const cfPivaRegex = /^[0-9]{11}$/;


  return (
    <>
      <div className={'align-items-center back-col'}>
        <div className={"image-div"}>
          <Row>
            <Col md={6}>
              <div className={'image-div-content'}>
                <div className={'align-content-start p-4'}>
                  <h1 className={'default-color'}>Hai bisogno di un finanziamento?</h1>
                  <p className={'default-color p-image'}>
                    {' '}
                    Compila il modulo sottostante ed in tempo reale ti diremo se la tua richiesta potrà essere
                    finanziata.{' '}
                  </p>
                  <a href="#formStart" type="button" className="custom-button-form">
                    Compila il form
                  </a>
                </div>
              </div>
            </Col>
          </Row>
        </div>
        <Col md={12}>
          <div className={'form-div'} id="formStart">
            <Formik
              initialValues={{
                periodicita: '',
                preammortamento: '',
                contattoTelefonico: '',
                codiceFiscale: '',
                nominativoRiferimento: '',
                prodotto: '',
                finalita: '',
                importo: 0,
                durata: '',
                recaptcha: ''
              }}
              validationSchema={Yup.object().shape({
                importo: Yup.number().required('Campo obbligatorio').min(50000, 'Importo non ammissibile').max(300000, 'Importo non ammissibile'),
                codiceFiscale: Yup.string()
                  .required('Campo obbligatorio')
                  .matches(cfPivaRegex, "Formato non valido")
                  .test('codiceFiscale', 'Codice Fiscale o Partita IVA non trovata', async function aidaFunc(value) {
                     let isValidAida = true;
                    if (document.activeElement.id === "codiceFiscale" && cfPivaRegex.test(value)) {
                      await aidaValidation(K.serviceUrl, value).then(value1 => {
                        setAida(undefined)
                         isValidAida = value1.aidaPIva === value
                         setAida(value1.aidaPIva === value);
                        setAidaRes(value1);
                      })
                    }
                    if(isValidAida === false){
                      return this.createError({ message: `Codice Fiscale o Partita IVA non trovata`,
                        path: 'codiceFiscale' });

                    }
                    return isValidAida;
                  })
                ,
                contattoTelefonico: Yup.string().matches(phoneRegExp, 'Numero non valido'),
                prodotto: Yup.string().required('Campo obbligatorio'),
                finalita: Yup.string().required('Campo obbligatorio'),
                durata: Yup.string().when('finalita', {
                    is: undefined,
                    then: Yup.string().required("Inserire una finalità per scegliere la durata"),
                    otherwise: Yup.string().required("Campo obbligatorio"),
                  }
                ),
                periodicita: Yup.string().required('Campo obbligatorio'),
                preammortamento: Yup.string().required('Campo obbligatorio'),
                recaptcha: Yup.string().required()
              })}
              onSubmit={fields => {
                console.log(fields);
                richiestaPostFromForm(K.serviceUrl, fields)
                  .then(value => {
                    console.log(value);
                    window.location.href = '/entando-de-app/en/richiesta_fido_esito.page?id=' + value.id;
                  })
                  .catch(reason => console.log(reason));
              }}
              render={({
                         errors,
                         status,
                         touched,
                         isValid,
                         setFieldValue,
                         handleSubmit,
                         handleChange,
                         handleBlur,
                         values
                       }) => (
                <Form onSubmit={handleSubmit}>
                  <DivWhiteRoundedShadowed className={'white-div align-content-center'}>
                    <Row className={'align-to-end'}>
                      <Col md={1}>
                        <p className={'from-text-header'}>ANAGRAFICA</p>
                      </Col>
                      <Col md={11}>
                        <p className={'from-text-header'}>*Campi Obbligatori</p>
                      </Col>
                    </Row>
                    <Row>
                      <Col>
                        <div className="form-group mb-4">
                          <label htmlFor="codiceFiscale">P.IVA/Codice Fiscale*</label>
                          <Field
                            id="codiceFiscale"
                            name="codiceFiscale"
                            type="text"
                            className={'form-control' + (errors.codiceFiscale && touched.codiceFiscale ? ' is-invalid' : '')}
                          />
                          <ErrorMessage name="codiceFiscale" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="nominativoRiferimento">Nominativo Riferimento</label>
                          <Field name="nominativoRiferimento" type="text" className={'form-control'}/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="contattoTelefonico">Contatto Telefonico</label>
                          <Field
                            name="contattoTelefonico"
                            type="phone"
                            className={'form-control' + (errors.contattoTelefonico && touched.contattoTelefonico ? ' is-invalid' : '')}
                          />
                          <ErrorMessage name="contattoTelefonico" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="importo">Importo Richiesto*</label>
                          {/*  type="number"*/}
                          {/*  onChange={val => val.floatValue}*/}
                          {/*  className={'form-control' + (errors.importo && touched.importo ? ' is-invalid' : '')}*/}

                          <NumericFormat name="importo"
                                         thousandSeparator={"."}
                                         decimalSeparator={","}
                                         onBlur={(e) => handleBlur(e)}
                                         onValueChange={(values) => {
                                           const { formattedValue, value } = values
                                           setFieldValue('importo', value)
                                         }}
                                         className={'form-control' + (errors.importo && touched.importo ? ' is-invalid' : '')} >
                          </NumericFormat>
                          <p className="small text-muted">
                            Importo minimo 50.000 / Importo massimo 300.000
                          </p>
                          <ErrorMessage name="importo" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="finalita">Finalità*</label>
                          <Field
                            as="select"
                            name="finalita"
                            className={'form-select' + (errors.finalita && touched.finalita ? ' is-invalid' : '')}
                          >
                            <option className={'custom-select-option'} value={''}>
                              Seleziona una Finalità
                            </option>
                            {K.finalita.map(option => (
                              <option key={option.key} className={'custom-select-option'} value={option.value}>
                                {option.key}
                              </option>
                            ))}
                          </Field>
                          <ErrorMessage name="finalita" component="div" className="invalid-feedback"/>
                        </div>
                      </Col>
                      <Col>
                        <div >
                          <p>Ragione Sociale : {!errors.codiceFiscale && aidaRes && aidaRes.aidaRagioneSociale}</p>
                          <p>Pec : {!errors.codiceFiscale && aidaRes && aidaRes.aidaPec}</p>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="prodotto">Prodotto*</label>
                          <Field
                            as="select"
                            name="prodotto"
                            className={'form-select' + (errors.prodotto && touched.prodotto ? ' is-invalid' : '')}
                          >
                            <option className={'custom-select-option'} value={''}>
                              Seleziona un Prodotto
                            </option>
                            {K.prodotto.map(option => (
                              <option key={option.key} className={'custom-select-option'} value={option.value}>
                                {option.key}
                              </option>
                            ))}
                          </Field>
                          <ErrorMessage name="prodotto" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="durata">Durata* </label>
                          <Field
                            as="select"
                            name="durata"
                            className={'form-select' + (errors.durata && touched.durata ? ' is-invalid' : '')}
                          >
                            <option className={'custom-select-option'} value={''}
                                    disabled={values.finalita === undefined || values.finalita === ''}>
                              Seleziona una Durata
                            </option>
                            {values.finalita === "INVESTIMENTI" ? K.durata.map(option => (
                                <option key={option.key} className={'custom-select-option'} value={option.value}
                                        disabled={values.finalita === undefined || values.finalita === ''}>
                                  {option.key}
                                </option>
                              )) :
                              values.finalita !== "INVESTIMENTI" && K.durata.filter(value => value.value === '60').map(option => (
                                <option key={option.key} className={'custom-select-option'} value={option.value}
                                        disabled={values.finalita === undefined || values.finalita === ''}>
                                  {option.key}
                                </option>
                              ))}
                          </Field>
                          <ErrorMessage name="durata" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group mb-4">
                          <label htmlFor="preammortamento">Preammortamento*</label>
                          <Field
                            as="select"
                            name="preammortamento"
                            className={'form-select' + (errors.preammortamento && touched.preammortamento ? ' is-invalid' : '')}
                          >
                            <option className={'custom-select-option'} value={''}>
                              Seleziona Preammortamento
                            </option>
                            {K.preammortamento.map(option => (
                              <option key={option.key} className={'custom-select-option'} value={option.value}>
                                {option.key}
                              </option>
                            ))}
                          </Field>
                          <ErrorMessage name="preammortamento" component="div" className="invalid-feedback"/>
                        </div>
                        <div className="form-group custom-margin">
                          <label htmlFor="periodicita">Periodicità*</label>
                          <Field
                            as="select"
                            name="periodicita"
                            className={'form-select' + (errors.periodicita && touched.periodicita ? ' is-invalid' : '')}
                          >
                            <option className={'custom-select-option'} value={''}>
                              Seleziona una Periodicità
                            </option>

                            {K.periodicita.map(option => (
                              <option key={option.key} className={'custom-select-option'} value={option.value}>
                                {option.key}
                              </option>
                            ))}
                          </Field>
                          <ErrorMessage name="periodicita" component="div" className="invalid-feedback"/>
                        </div>
                      </Col>
                    </Row>
                  </DivWhiteRoundedShadowed>
                  {/*6LdFHCshAAAAAGqnoDlJRYgjNWnL-DSRAiesUwbx*/}
                  <div className="text-xs-end">
                    <ReCAPTCHA
                      className={errors.recaptcha && touched.recaptcha ? 'rec-div-invalid' : 'rec-div'}
                      sitekey="6LdP908iAAAAAAyZ8deXI2VsUkchMhGgVB8w4gR0"
                      render="explicit"
                      onChange={response => {
                        setFieldValue('recaptcha', response);
                      }}
                    />
                    {errors.recaptcha && touched.recaptcha && <p>Verifica obbligatoria</p>}
                    {/*<div className="g-recaptcha" data-sitekey="6LdFHCshAAAAAPA70VJk5T9LC8XzzemwybZ0r7Oj"/>*/}
                  </div>

                  <Row>
                    <Col className={'align-to-end mt-2'}>
                      <button type="submit" className="custom-button-richiesta">
                        {' '}
                        Invia la tua richiesta
                      </button>
                    </Col>
                  </Row>
                </Form>
              )}
            />
          </div>
        </Col>
      </div>
    </>
  );
};
